/* remove all the bullets, borders and padding from the default list styling */
#lang_sel {
  height: 32px;
  position: relative;
  font-family: verdana, arial, sans-serif;
  display: inline-block;
  /* style the table so that it takes no part in the layout - required for IE to work */
  /* style the top level hover */
}
#lang_sel ul {
  padding: 0 !important;
  margin: 0 !important;
  list-style-type: none !important;
  /* hide the sub levels and give them a positon absolute so that they take up no room */
}
#lang_sel ul li:hover ul, #lang_sel ul a:hover ul {
  visibility: visible;
}
#lang_sel ul ul {
  width: 149px;
  visibility: hidden;
  position: absolute;
  height: 0;
  top: 25px;
  left: 0;
  border-top: 1px solid #cdcdcd;
}
#lang_sel ul ul a {
  background: #ffffff;
  color: #444444;
  height: auto;
  line-height: 1em;
  padding: 5px 10px;
  border-width: 0 1px 1px 1px;
}
#lang_sel ul ul a:visited {
  background: #ffffff;
  color: #444444;
  height: auto;
  line-height: 1em;
  padding: 5px 10px;
  border-width: 0 1px 1px 1px;
}
#lang_sel li {
  float: left;
  width: 149px;
  position: relative;
  padding: 0 !important;
  margin: 0 !important;
  list-style-type: none !important;
}
#lang_sel li:before {
  content: '' !important;
}
#lang_sel a {
  display: block;
  font-size: 11px;
  text-decoration: none !important;
  color: #444444;
  border: 1px solid #cdcdcd;
  background: #fff;
  padding-left: 10px;
  line-height: 24px;
}
#lang_sel a:visited {
  display: block;
  font-size: 11px;
  text-decoration: none !important;
  color: #444444;
  border: 1px solid #cdcdcd;
  background: #fff;
  padding-left: 10px;
  line-height: 24px;
}
#lang_sel a.lang_sel_sel {
  background: url(/betheme/wp-content/plugins/sitepress-multilingual-cms/res/img/nav-arrow-down.png) #fff right no-repeat;
  color: #444;
}
#lang_sel a.lang_sel_sel:hover {
  text-decoration: none;
  color: #000;
}
#lang_sel table {
  position: absolute;
  top: 0;
  left: 0;
  border-collapse: collapse;
}
#lang_sel a:hover, #lang_sel ul ul a:hover, #lang_sel :hover > a, #lang_sel ul ul :hover > a {
  color: #000;
  background: #eee;
}
#lang_sel img.iclflag {
  width: 18px;
  height: 12px;
  position: relative;
  top: 1px;
}
#lang_sel.icl_rtl {
  text-align: right;
  direction: rtl;
}
#lang_sel.icl_rtl .lang_sel_sel {
  padding-right: 14px;
}

/* make the second level visible when hover on first level list OR link */
#lang_sel_footer {
  margin: 0;
  padding: 7px;
  text-align: center;
  font: 11px Verdana, sans-serif;
  min-height: 15px;
  clear: both;
  background-color: #fff;
  border: 1px solid #cdcdcd;
}
#lang_sel_footer ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
#lang_sel_footer ul li {
  display: inline;
  margin: 0 1px 0 0;
  padding: 0;
  white-space: nowrap;
  line-height: 25px;
}
#lang_sel_footer ul li img {
  position: relative;
  top: 1px;
  width: 18px;
  height: 12px;
}
#lang_sel_footer ul li a {
  text-decoration: none;
  padding: 5px 10px;
}
#lang_sel_footer ul li a:visited {
  text-decoration: none;
  padding: 5px 10px;
}

#wpml_credit_footer {
  width: 100%;
  margin: 10px 0;
  padding: 0;
  text-align: center;
  font-size: 11px;
}

/* remove all the bullets, borders and padding from the default list styling */
#lang_sel_list {
  height: 32px;
  position: relative;
  z-index: 99;
  font-family: verdana, arial, sans-serif;
  /* style the table so that it takes no ppart in the layout - required for IE to work */
