  padding: 3px 10px;
}
#lang_sel_list.lang_sel_list_vertical li {
  width: 149px;
}
#lang_sel_list.lang_sel_list_vertical a {
  border: 1px solid #cdcdcd;
  border-top-width: 0;
  padding-left: 10px;
}
#lang_sel_list.lang_sel_list_vertical a:visited {
  border: 1px solid #cdcdcd;
  border-top-width: 0;
  padding-left: 10px;
}
#lang_sel_list table {
  position: absolute;
  top: 0;
  left: 0;
  border-collapse: collapse;
}
#lang_sel_list img.iclflag {
  width: 18px;
  height: 12px;
  position: relative;
