/* #Basic Styles
================================================== */
	html {
		height: 100%;
	}
	body {
		-webkit-font-smoothing: antialiased; /* Fix for webkit rendering */
		-webkit-text-size-adjust: 100%;
 }

/* #Typography
================================================== */

	h1 a, h2 a, h3 a, h4 a, h5 a, h6 a { font-weight: inherit; }
	h1 a:hover, h2 a:hover, h3 a:hover, h4 a:hover, h5 a:hover, h6 a:hover { text-decoration: none; }
	h1 { margin-bottom: 15px; }
	h2 { margin-bottom: 15px; }
	h3 { margin-bottom: 15px; }
	h4 { margin-bottom: 15px; }
	h5 { margin-bottom: 15px; }
	h6 { margin-bottom: 15px; }

	p { margin: 0 0 15px 0; }

	em, i 			{ font-style: italic; }
	b, strong 		{ font-weight: bold; }
	big, .big 		{ font-size: 110%; line-height: 180%; }
	
	dt { font-weight:bold;}


/* #Links
================================================== */
	a, a:visited, a:focus { text-decoration: none; outline: 0; }
	a:hover { text-decoration: underline; }
	p a, p a:visited { line-height: inherit; }


/* #Images
================================================== */

	img.scale-with-grid,
	#Content img {
		max-width: 100%;
		height: auto;
	}
	
	.google-map img {
		max-width:none !important;
	}
	
	iframe {
		max-width: 100%;
	}


/* #Buttons
================================================== */

	a.tp-button{
		background: none;
		font-family: inherit !important;
		height: auto !important;
		line-height: inherit !important;
		border-radius: 0px;
		-moz-border-radius: 0px;
		-webkit-border-radius: 0px;
		letter-spacing: 0 !important;
		text-shadow: 0 0 0 !important;
		box-shadow: 0 0 0 !important;
		font-weight: normal !important;
		border-width: 1px !important;
	}

	a.button, a.tp-button, button, input[type="submit"], input[type="reset"], input[type="button"] {
		display: inline-block;
		padding: 11px 20px;
		margin-bottom: 15px;
		cursor: pointer;
		margin-right: 7px;
		border: 0;
		-webkit-border-radius: 5px;
		border-radius: 5px;
		position: relative;
		overflow: hidden;
		-webkit-box-shadow:inset 0 0 0 1px rgba(0,0,0,.03);
		box-shadow:inset 0 0 0 1px rgba(0,0,0,.03);
		background-image: url(../images/box_shadow_button.png);
		background-repeat: repeat-x;
	}
	a.button_js {
		padding: 0;
	}
	
	a.button:after, a.tp-button:after, button:after, input[type="submit"]:after, input[type="reset"]:after, input[type="button"]:after {
		content: ""; position: absolute; left: 0; top: 0; height: 100%; width: 0; z-index: 1; -webkit-transition: all 0.3s; -moz-transition: all 0.3s; transition: all 0.3s; background: rgba(0,0,0,.05);
	}
	
	a.button.button_large,
	a.tp-button.button_large,
	button.button_large,
	input[type="submit"].button_large,
	input[type="reset"].button_large,
	input[type="button"].button_large {
		font-size: 110%;
		line-height: 110%;
	}
	
	/* Button label & icon */
	a.button .button_label { display: block; float: left; position: relative; z-index: 2; }	
	a.button .button_icon { display: block; float: left; background: rgba(0,0,0,.05); }
	
	a.button .button_icon i { color: rgba(0,0,0,.5); }
	
	/* Button icon left */
	a.button.button_left .button_icon,
	a.tp-button.button_left .button_icon { float: left; }
	
	a.button.button_right .button_icon,
	a.tp-button.button_right .button_icon { float: right; }
	
	/* Paddings */
	a.button .button_label { padding: 11px 20px; }	
	a.button .button_icon { padding: 11px 13px; }
	
	a.button_large .button_label { padding: 15px 30px; }
	a.button_large .button_icon { padding: 15px 13px; }
	
	/* Hover */
	a:hover.button, a:hover.tp-button {
		text-decoration: none;
	}
	a.button:hover:after, a.tp-button:hover:after, button:hover:after, input[type="submit"]:hover:after, input[type="reset"]:hover:after, input[type="button"]:hover:after {
		width: 100%;
	}

	/* Fix for odd Mozilla border & padding issues */
	button::-moz-focus-inner, input::-moz-focus-inner {
    	border: 0;
    	padding: 0;
	}
	
/* Red */
a.button.button_red, a.tp-button.red {
	background-color: #e7432b;
	color: #fff;
}

/* Blue */
a.button.button_blue, a.tp-button.blue {
	background-color: #2e96db;
	color: #fff;
}

/* Turquoise */
a.button.button_turquoise, a.tp-button.turquoise {
	background-color: #22e387;
	color: #fff;
}

/* Yellow */
a.button.button_yellow, a.tp-button.yellow {
	background-color: #face43;
	color: #fff;
}

/* Grey */
a.button.button_grey, a.tp-button.grey {
	background-color: #8f8f8f;
	color: #fff;
}

/* Navy */
a.button.button_navy, a.tp-button.navy {
	background-color: #3b5982;
	color: #fff;
}

/* Orange */
a.button.button_orange, a.tp-button.orange {
	background-color: #ff8125;
	color: #fff;
}

/* Green */
a.button.button_green, a.tp-button.green {
	background-color: #88be4c;
	color: #fff;
}

/****************************************************/

/* Button darkgrey */
a.tp-button.darkgrey {
	background-color: transparent;
	color: #545454 !important;
	border-color: #545454;
}
a:hover.tp-button.darkgrey {
	background-color: #545454;
	color: #fff !important;
}

/* Button lightgrey */
a.tp-button.lightgrey {
	background-color: transparent;
	color: #fff;
	border-color: #fff;
}
a:hover.tp-button.lightgrey {
	background-color: #fff;
	color: #545454 !important;
}
	
/* #Typography
==================================================================================================== */

	/* Dropcaps */
	.dropcap { display: inline-block; float: left; width: 35px; height: 35px; line-height: 35px; font-size: 20px; margin: 0 10px 5px 0; text-align: center; -webkit-border-radius: 5px; border-radius: 5px; background-image: url(../images/stripes/stripes_10_w.png); }
	.dropcap_circle { -webkit-border-radius: 100%; border-radius: 100%; }


	/* Highlight */
	.highlight { padding: 1px 7px; -webkit-border-radius: 3px; border-radius: 3px; background-image: url(../images/stripes/stripes_10_w.png); }
	.highlight.highlight_image { padding: 0 2px 7px; }
	
	
	/* Tooltip */
	.tooltip{ display: inline; position: relative; cursor: help; border-bottom-width: 1px; border-style: dotted; }
	.tooltip:hover:after, 
	.tooltip.hover:after { content: attr(data-tooltip); font-size: 13px; line-height: 18px; text-align: center; -webkit-border-radius: 5px; border-radius: 5px; padding: 5px 15px; width: 140px; position: absolute; left: 50%; bottom: 110%; z-index: 98; margin-left: -85px;  background-image: url(../images/stripes/stripes_10_w.png); }
	
	.wpml-languages .tooltip:hover:after, 
	.wpml-languages .tooltip.hover:after { top:40px; bottom:auto; }
	.wpml-languages:not(.disabled) .tooltip { cursor: default;}
	.wpml-languages:not(.disabled) .tooltip:hover:after, 
	.wpml-languages:not(.disabled) .tooltip.hover:after { display:none !important; }


	/* Blockquote */
	blockquote { font-size: 17px; line-height: 31px; font-family: Patua One; display: block; height: auto; margin: 0 0 35px 25px; top: 15px; position: relative; background: url(../images/stripes/textline.png) repeat-y; }
